
.img-pf{
    width: 12vw;
    height: 20vh;
}
#portfolio{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5rem;
}

 .portfolio_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5rem;
 }

 .portfolio_item{
    height: 40vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(238,210,255) ;
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: 2sec;
 }

 .portfolio_item:hover{
    background: transparent;
    border-color: #005555;
    transform: scale(1.1);
 }

 .portfolio_item-image{
    border-radius: 1rem;
    overflow: hidden;
 }

 .portfolio_item h3{
    margin: 1.2rem 0 2rem;
 }

 .portfolio_item-ct{
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
     
 }

 .btn-s {
    display: inline-block;
    background: white;
    color: black;
    padding: 0.3rem 0.7rem;
    border-radius: 1rem;
    text-decoration: none;
  }
 
  .btn-s:hover {
    background: black;
    cursor: pointer;
    /* border: 0.3rem solid #005555; */
    color: white;
    border-radius: 0.5rem;
    transform: scale(1.05);
  }


 /* media-query */

 @media screen and (max-width:1024px) {
    .portfolio_container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
    .img-pf{
      width: 22vw;
      height: 20vh;
  }
 }
 @media screen and (max-width:600px) {
    .portfolio_container{
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .img-pf{
      width: 22vw;
      height: 20vh;
  }
    
 }
