.about {
  background-color: #fff;
  margin-top: 4rem;
  width: 90vw;
  height: 80vh;
  padding: 0.5rem 3.5rem;
}

.about-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
}
hr {
  border: 6px dotted #005555;
  width: 70%;
  border-top: none;
}
.hr-end {
  margin-top: 3rem;
}
.heading {
  text-align: center;
}
.heading h2 {
  font-weight: bolder;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}
.heading span {
  color: rgb(47, 188, 47);
  font-weight: bold;
  font-size: 1.2rem;
}
.about-img img {
  width: 30vw;
  height: 55vh;
  border-radius: 0.5rem;
}

.about-text p {
  font-size: var(--p-font);
  font-weight: 400;
  text-align: justify;
  line-height: 2rem;
}
.about-text a {
  text-decoration: none;
}

.information {
  margin: 1rem 0 1.4rem;
}

.information .info-box {
  display: flex;
  align-items: center;
  margin-bottom: 1.4rem;
}

.information .info-box .bx {
  font-size: 22px;
}
.info-box {
  display: flex;
  align-items: center;
}
.ic {
  height: 1.5rem;
  width: 1.5rem;
  margin: 0%;
}

.information .info-box span {
  font-weight: 400;
  margin-left: 1rem;
}
.btn {
  display: inline-block;
  background: #005555;
  color: #fff;
  padding: 0.7rem 1.3rem;
  border-radius: 0.5rem;
}

.btn:hover {
  background: white;
  cursor: pointer;
  border: 1px solid #005555;
  color: black;
}

@media (max-width: 768px) {
  .about-container {
    flex-direction: column;
    
  }


  .about-img img {
    width: 40vh;
  }
}
