
#contact{
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.img-ct{
    width: 3vw;
    height: 5vh;
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact_options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact_option{
    background-color: aliceblue;
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: 1sec;
}

.contact_option:hover{
    background: transparent;
    border-color: rgb(64, 64, 150);
    transform: scale(1.05);

}
.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
    text-decoration: none;
}

/* Form */

form{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid #005555;
    resize: none;
    color: black;
}



/* media_Query */

@media screen and (max-width:1024px) {
    .contact_container{
        grid-template-columns: 1fr ;
        gap: 2rem;
    }
 }
 @media screen and (max-width:600px) {
    .contact_container{
        width: 12rem;
    }

    
 }
