footer{
    all: none
  }

footer{
    background: rgb(231, 231, 192);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
}
footer a{
    color: black;
    text-decoration: none;
}

.footer_logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.f-links{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    list-style: none;
}

.f-links li a{
 font-size: 1rem;
 font-weight: 450;
}
.f-links li a:hover{
    opacity: 0.7;
}

.footer_socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}

.footer_socials a{
    /* background: ;
    color: ; */
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.copyright{
    margin-bottom: 1.5rem;
    color: gray;
    
}

.footer_socials img{
    width: 30px;
    height: 30px;
    transition: .4sec;
}
.footer_socials img:hover{
    transform: scale(1.3);
}

/* media-Query */

@media  screen and (max-width:600px) {

    .f-links{
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer_socials{
        margin-bottom: 2.6rem;
    }
    
}