

.Intro {
    display: flex;
    height: 77vh;
    margin-top: 6rem;
    padding: 0.5rem 3.5rem;
  }
  
  /* left side styling */
  .i-left {
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
  }
  .i-name {
    display: flex;
    flex-direction: column;
    gap: 0.25rem;
  }
  .i-name > :nth-child(1) {
    color: var(--black);
    font-weight: bold;
    font-size: 2.5rem;
  }
  .i-name > :nth-child(2) {
    color: rgb(64, 64, 150);
    font-weight: bold;
    font-size: 3rem;
  }
  .i-name > :nth-child(3) {
    font-weight: 400;
    font-size: 14px;
    color: var(--gray);
    margin-top: 10px;
  }
  .i-button {
    width: 7rem;
    height: 3rem;
  }
  .i-icons {
    margin-top: 3rem;
    display: flex;
    gap: 0rem;
  }
  .i-icons > * {
    transform: scale(0.5);
  }
  .i-icons > *:hover {
    cursor: pointer;
  }
  .i-icons img{
    width: 3.5rem;
    height: 4rem;
  }
  /* right side styling */
  .i-right {
    flex: 1;
    position: relative;
  }
  .i-right > * {
    position: absolute;
    z-index: 1;
  }
  .i-right > :nth-child(1) {
    /* transform: scale(0.5); */
    width: 30vw;
    height: 60vh;
    left: 16%;
    top: 2rem;
    bottom: 35%;
    background-color: #edd0ff;
    border-radius: 2rem;
    border: 0.3rem solid rgb(64, 64, 150);
  }
  .i-right > :nth-child(2) {
    transform: scale(0.62);
    top: -19%;
    left: -24%;
    border-radius: 50%;
    padding: 0;
  }
  
  /* blur */
  .blur {
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index: -9;
    top: -18%;
    /* left: 1%; */
    filter: blur(72px);
  }
  
  @media screen and (max-width: 480px) {
    .Intro {
      flex-direction: column;
      gap: 2rem;
      height: 64rem;
    }
  
    .i-right {
      transform: scale(0.8);
    }
    .i-right > :nth-child(1){
      width: 80vw;
      left: -16%;
    }
    .i-right .blur {
      display: none;
    }
    .floating-div:nth-of-type(1){
      /* display: none; */
      top: -7rem !important;;
    }
  }